<template>
  <div class="bill">
      <div class="bill-top">
          <div class="bill-top-logo">
             <van-notice-bar
                text="爱琦记账本--------------v0.0.1-------------测试版本--------这是最终的前端框架了，最终得了。再做就快吐了。真的快吐了。我的天！"
                left-icon="volume-o"
                />
          </div>
          <div class="bill-top-details">
            <div class="bill-top-details-date" @click="show = true">
                {{selectYear}} <br><font>{{selectMonth}}</font>
            </div>
            <div class="bill-top-details-export">
                收入<br><font>{{BillSumImport}}</font>
            </div>
            <div class="bill-top-details-import">
                支出<br><font>{{BillSumExport}}</font>
            </div>
          </div>    
      </div>
      <div class="bill-detail">
          <div class="bill-detail-top">
          </div>
          <div class="bill-detail-body">
            <demo-section name="pull-refresh">
                <van-pull-refresh
                v-model="list.refreshing"
                @refresh="onRefresh"
                >
                <van-list
                        v-model="list.loading"
                        :finished="list.finished"
                        :finished-text="finishedText"
                        @load="onLoad"
                    >
                    <van-cell
                        v-for="item in list.items"
                        :key="item">
                        <div class="list-Date">
                            <div class="list-Date-Detail">{{item.BillDate}}&nbsp&nbsp&nbsp&nbsp{{item.BillDay}}</div>
                            <div>收入:{{item.BillImport}}&nbsp&nbsp&nbsp&nbsp支出:{{item.BillExport}}</div>
                        </div>  
                        <div class="list-Detail"  v-for="detailItem in item.Detail" :key="detailItem">
                            <img :src="iconList[detailItem.BillIcon]">
                            <div class="list-Detail-Title">{{detailItem.BillTitle}}</div>
                            <div class="list-Detail-Money">{{detailItem.BillMoney}}</div>
                        </div>
                    </van-cell>
                    </van-list>
                </van-pull-refresh>
            </demo-section>
          </div>
           <div class="bill-detail-top">
          </div>
      </div>
      <div class="bill-Record" @click="showBill = true">
          <img src="../assets/add.png">
      </div >
      <div>
        <van-popup
            v-model="show"
            position="bottom"
            >
            <van-datetime-picker
                v-model="currentDate"
                type="year-month"
                @confirm="selYearMonth"
                @cancel="show = false"
            />
        </van-popup>
         <van-popup
            v-model="showBill"
            position="bottom"
            >
            <div class="selbill">
                <bill @closeBill="toCloseBill" @openDateTime="OverLayClick"></bill>
            </div>
        </van-popup>
        <div  v-if="OverLay" class="van-overlay" style="z-index: 2000;"></div>
    </div>
  </div>
</template>
<script>
import { selectByMonth,insertData } from '../api/getData'
import {List,Toast ,Tab, Tabs, PullRefresh,Cell,NoticeBar,Popup,DatetimePicker} from 'vant';
import bill from './bill.vue';
export default {
  name: 'alibabaPay',
  components: {
      [List.name]: List,
      [Toast.name]: Toast,
      [Tab.name]: Tab,
      [Tabs.name]: Tabs,
      [PullRefresh.name]: PullRefresh,
      [Cell.name]: Cell,
      [NoticeBar.name]: NoticeBar,
      [Popup.name]: Popup,
      [DatetimePicker.name]: DatetimePicker ,
      bill
  },
  data() {
    return {
      list: {
        items: [],
        refreshing: false,
        loading: false,
        error: false,
        finished: false
      },
      allItems: [],
      selectYear: "",
      selectMonth: "",
      count: 0,
      isLoading: false,
      basicUsage:"123",
      text: "刷新次数",
      success: "刷新成功",
      finishedText: "没有更多了",
      show: false,
      showBill: false,
      currentDate: new Date(),
      OverLay: false,
      iconList:{
          1: require("../assets/food-active.png"),
          2: require('../assets/clothes-active.png'),
          3: require('../assets/shopping-active.png'),
          4: require('../assets/recreation-active.png'),
          5: require('../assets/heart-active.png')
      },
      BillSumImport: "",
      BillSumExport:""
    };
  },
  watch(){
      
  },
  created(){
      this.init();
  },
  methods: {
    init() {
        this.selectYear = "2019年"
        this.selectMonth = "01月"
        this.getDta()
    },
    getDta(){
        let lastDate = new Date()
        let year = this.selectYear.substring(0,4),month = this.selectMonth.substring(0,2)
        let lastDay = new Date(year,month,0).getDate()
        setTimeout(async() => { 
            let ret = await selectByMonth({ fromDate: `${year}-${month}-01`, toDate: `${year}-${month}-${lastDay}`})
            this.allItems = ret.data
            this.BillSumImport = ret.BillSumImport
            this.BillSumExport = ret.BillSumExport
        })
    },
    inserToData( mBillDate, mBillType, mBillMoney, mBillRemark){
       
            let ret =  insertData({BillDate:mBillDate, BillType:mBillType , BillMoney:mBillMoney , BillRemark:mBillRemark })
    
    },
    selYearMonth(){ 
        this.selectYear = this.currentDate.getFullYear() + "年"
        let selectMonth = parseInt(this.currentDate.getMonth())  + 1
        selectMonth = selectMonth<10 ? "0"+selectMonth : selectMonth 
        this.selectMonth =  selectMonth + "月"
        this.show = false;
        this.onRefresh()
    },
     onLoad() {
      const list = this.list;
      const showLength = list.items.length
      setTimeout(() => {
          //list.finished = true;
            for (let i = showLength; i < (showLength+6); i++) {
                if (list.items.length >= this.allItems.length)
                {
                    list.loading = false;
                    list.finished = true;
                    return
                }
                list.items.push(this.allItems[i]);
            }
            list.loading = false;
            if (list.items.length >= this.allItems.length)
            {
                list.finished = true;
            }
      }, 500);
    },
    onRefresh() {
      const list = this.list;
      this.getDta()
      setTimeout(() => {
        list.items = [];
        list.error = false;
        list.finished = false;
        list.refreshing = false;
        window.scrollTo(0, 10);
      }, 300);
    },
    async toCloseBill( mBillDate, mBillType, mBillMoney, mBillRemark){
        this.showBill = false
        if (arguments.length === 4){
            await this.inserToData( mBillDate, mBillType, mBillMoney, mBillRemark);
             await this.onRefresh();

        }
    },
    OverLayClick(value){
        this.OverLay = value
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
html,body{
	height: 100%;
	width: 100%;
}
#app{
    color: rgb(30, 41, 61);
    height: 100%;
    overflow: hidden;
     background: #BEBECA;
}
.bill{
    display: flex;
    flex-direction: column; 
    height: 100%;
    &-top{
        height: 95px;
        background: #FF69B4;
        &-details{
            //background: black;
            display: flex;
            flex-direction: row;
            font{
                font-weight: bold;
                font-size: 23px;
            }
            &-date{
                padding-left: 5%;
                padding-top: 2px;
                padding-bottom: 2px;
                width: 20%;
                //background: brown;
            }
            &-import{
                padding-left: 5%;
                padding-top: 2px;
                padding-bottom: 2px;
                width: 40%;
                //background:cadetblue;
            }
            &-export{
                padding-left: 5%;
                padding-top: 2px;
                padding-bottom: 2px;
                width: 40%;
                //background:cornflowerblue;
            }
        }
    }
    &-detail{
    display: flex;
    flex-direction: row;
    flex-grow:1;
        &-top{
            height: 20px;
             flex-grow:1;
            background: #FF69B4;
        }
        &-body{

            flex-grow:18;
            background: white;
            border-top: yellow 2px solid;
        }
    }
    &-Record{
         height:30px;
         background:white;
         text-align: center;
         line-height: -10%;
         padding: 2px;
    }
    &-DateSelect{
        height:60px;
        background: 100px;
    }
}
  .van-pull-refresh {
      overflow:scroll;
      height: 100%;
  }
  .selbill{
      height: 442px;
      background: white;
  }
.list-Date{
    font-size: 13px;
    color: #BEBECA;
    display: flex;
    flex-direction: row;
    &-Detail{
        flex-grow: 1;
    }
}
.list-Detail{
    margin-top: 15px;
    margin-bottom: 5px;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-weight: bold;
    &-Title{
        flex-grow: 1;
        padding-left: 10px;
    }
    &-Money{
       

    }
}
</style>
